.banner {
  height: 365px;
  padding: 0 40px;
  background-image: url(63e94e90092a737d9fde2225af0d434e.png);
}
.banner .tit1 {
  color: #fff;
  font-size: 40px;
  padding-top: 48px;
}
.banner .tit2 {
  color: #fff;
  font-size: 40px;
  margin: 20px 0;
}
.banner .search {
  height: 68px;
  padding: 6px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  display: flex;
}
.banner .search label {
  display: block;
  background: #f2f3f7;
  border-radius: 10px 0 0 10px;
  width: 162px;
  height: 67px;
  line-height: 68px;
  text-align: center;
}
.banner .search label span {
  font-size: 20px;
  margin: 10px;
}
.banner .search label i {
  font-size: 30px;
}
.banner .search div {
  background: #fff;
  width: 348px;
  height: 68px;
  padding: 0;
}
.banner .search div input {
  width: 100%;
  height: 100%;
  border: none;
  font-size: 20px;
  text-indent: 30px;
  padding: 0;
  margin: 0;
}
.banner .search button {
  width: 142px;
  height: 68px;
  border: none;
  border-radius: 0 10px 10px 0;
  background: #e8420a;
}
.banner .search button i {
  font-size: 30px;
  color: #fff;
}
